const axios = require("axios");
const PCors = "https://my.${region}.500apps.com/pcors";

class Data {
  constructor(url, jwt, params) {
    // eslint-disable-next-line
    let region = this.getRegion();
    this.url = eval("`" + url + "`");
    this.pcorsURL = eval("`" + PCors + "`");

    if (params) this.params = params;

    this.pcors = true;

    this.headers = {};

    this.offset = 0;
    this.limit = 50;

    // Enable Offset
    this.enableOffset = false;
  }

  /**
   * Function to make rest calls using axios API
   * @param {string} url    # url to make a call
   */
  async get() {
    let result = await axios.get(this.getPCors(this.url), this.getConfig());
    result = result.data;
    return result;
  }

  /**
   * Function to make POST call using axios API
   * @param {string} url    # url to make a call
   */
  async add(form, is_append_data) {
    let apps = await axios.post(this.getPCors(), !is_append_data ? { data: [form] } : form, this.getConfig()); // Data is not append in payload for python
    return apps.data;
  }

  /**
   * Function to make DELETE call using axios API
   * @param {string} id  Delete record based on ID
   * @param {string} is_append_id_path To append ID in where condition
   */
  async delete(id, is_append_id_path) {
    let url = is_append_id_path ? `${this.url}/${id}` : `${this.url}?where=id=${id}`; // Append id in url path for python api
    let result = await axios.delete(url, this.getConfig());
    return result;
  }

  /**
   * Function to make PUT call using axios API
   * @param {string} id  Update record based on ID
   * @param {string} formData  update  form id
   * @param {string} is_append_id_path To append ID in path params
   */
  async edit(id, formData, is_append_id_path) {
    if (!formData.id) formData.id = id;
    let url = is_append_id_path ? `${this.url}/${id}` : this.url; // Append id in url path for python api
    let apps = await axios.put(this.getPCors(url), formData, this.getConfig());
    return apps.data;
  }

  // Get bearer token
  getConfig() {
    return {
      headers: {
        ...{
          "x-api-key": window.apiKey,
          withCredentials: true,
        },
        ...this.getHeaders(),
      },
    };
  }

  // Get headers
  getHeaders() {
    if (Object.keys(this.headers).length) return this.headers;
    return {};
  }

  // Append PCORS url
  getPCors(url) {
    if (!url) url = this.url;

    // Return URL directly if pcors is disabled
    if (!this.pcors) return url;

    // Use base url if not present - detail URL eg: /flow/{id} can overwrite
    return `${this.pcorsURL}?url=${encodeURIComponent(url)}`;
  }

  // GET region
  getRegion() {
    // This will always return a region
    return window.region;
  }

  // To append conditions with given url
  append(url, params) {
    if (!url) url = "";
    let prepend = "?";
    if (url.indexOf("?") != -1) prepend = "&";

    url = `${url}${prepend}${params}`;
    return url;
  }
}
export default Data;



